<template>
	<view class='px-2'>
		<!-- <uni-list-item showNote :options="options">
			<view class="text-secondary" >
				<view class="d-flex a-center">
					<text>测试</text>
					17628199
					<text class="main-text-color">[默认]</text>
				</view>
				<view>
					挂得是 额度的 额度的
				</view> 
				<view>xxxx</view>
			</view>
		</uni-list-item> -->
		<!-- 添加滑动 -->
	<uni-swipe-action>
		<view v-for="item,index in list" :key="index" @click="choosePath(item)">
			<uni-swipe-action-item  class="mt-2 border-bottom" :options="options" @click="onClick($event,index)">
				<view class="text-secondary"  >
					<view class="d-flex a-center">
						<text>{{item.name}}</text>
						<text>{{item.phone}}</text>
						<text class="main-text-color" v-if="item.isDefault">[默认]</text>
					</view>
					<view>
						{{item.path}}
					</view> 
					<view>{{item.detailPath}}</view>
				</view>
			</uni-swipe-action-item>
		</view>
		
	</uni-swipe-action>
	</view>
</template>

<script>
	import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue'
	import uniSwipeAction from '@/components/uni-ui/uni-swipe-action/uni-swipe-action.vue'
	import uniSwipeActionItem from '@/components/uni-ui/uni-swipe-action-item/uni-swipe-action-item.vue'
	import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
	export default {
		components:{
			uniListItem,uniSwipeAction,uniSwipeActionItem
			
		},
		onNavigationBarButtonTap(e){
			//导航栏点击
			if(e.index==0){
				uni.navigateTo({
					url: '/pages/user-path-edit/user-path-edit'
				});
			}
		},
		onLoad(e){
			if(e.type==='choose'){
				this.isChoose=true
			}
		},
		data() {
			return {
				isChoose:false,
				// list:[
				// 	{
				// 		name:"summer",
				// 		phone:"32432453",
				// 		path:"四川省 成都市 郫县",
				// 		detailPath:"530街道",
				// 		isDefault:false
				// 	}
				// ],
				options:[
				  {
				      text: '修改',
				      style: {
				          backgroundColor: '#007aff'
				      }
				  }, {
				      text: '删除',
				      style: {
				          backgroundColor: '#dd524d'
				      }
				  }
				]
			}
		},
		methods: {
			...mapMutations(['delPath']),
			onClick(e,index){
			  // console.log('当前点击的是第'+e.index+'个按钮，点击内容是'+e.content.text)
			 console.log(2)
			  if(e.index==0){
				  //修改
				  
				  let obj = JSON.stringify({
					  index:index,
					  item:this.list[index]
				  })
				  uni.navigateTo({
				  	url:'/pages/user-path-edit/user-path-edit?data='+obj
				  })
			  }else if(e.index==1){
				  uni.showModal({
				  	content: '确定要删除该收货地址吗？',
				  	success:  (res)=> {
				  		if (res.confirm) {
							
				  			this.delPath(index)
				  			uni.showToast({
				  				title: '删除成功'
				  			});
				  		} else if (res.cancel) {
				  			
				  		}
				  	}
				  });
			  }
			},
			//选择收货地址
			choosePath(item){
				if(!this.isChoose)return;
				//通知订单提交页面修改收货地址、
				uni.$emit('choosePath',item)
				//关闭当前页面
				uni.navigateBack({
					delta:1
				})
			}
		},
		computed:{
			...mapState({
				list:state => state.path.list
			})
		}
	}
</script>

